
import {useState} from 'react';
import {Container} from '@material-ui/core';

function ElainKuvia(props) {


    return (
        
            <Container maxWidth="sm"
                        style={{textAlign: "center"}}>
                <img 
                src={props.kissa.file}
                
                width="70%" 
                alt="Kissa"/>
               
                </Container>
            
            
            
        
    )
}

export default ElainKuvia;