import {Button, Container} from '@material-ui/core';
import {Link} from 'react-router-dom';
import { v4 as uuid } from 'uuid';

function Mita(props) {
    return(
        <Container maxWidth="sm">
            <Button
            component={Link}
            to="/elain"
            variant="contained"
            color="primary"
            onClick={() => {props.setPaivitaKuvat((uuid()))}}
            fullWidth
            >Purrrr!!</Button>

            
        </Container>
    )
}

export default Mita;