import { useEffect, useState } from "react";
import ElainKuvia from "./components/ElainKuvia";
import Mita from "./components/Mita";
import Otsikko from "./components/Otsikko";
import {BrowserRouter as Router, Route} from "react-router-dom";
import { v4 as uuid } from 'uuid';


function App() {

  const [kissa, setKissa] = useState([])
 
  const [paivitaKuvat, setPaivitaKuvat] = useState(uuid());

  

  const haeTiedot = async () => {

    const kissa = await fetch("https://aws.random.cat/meow");
   
    const kissaKuva = await kissa.json();
    
    setKissa(kissaKuva);
    
    console.log(uuid());
  }

  useEffect(() => {

    haeTiedot();

  }, [paivitaKuvat])
  
  
  

  return (
    
    <Router>
      <Otsikko/>
     <Route path="/elain">
      <ElainKuvia kissa={kissa} />
     </Route>
     <Mita setPaivitaKuvat={setPaivitaKuvat} kissa={kissa}/>
    </Router>
  );
}

export default App;
